angular.module('SMART2').directive('smartCultureDate', ['APPCONSTANTS', function (APPCONSTANTS) {
    "use strict";
    var monthAbr = {
        january: 'Jan',
        february: 'Feb',
        march: 'Mar',
        april: 'Apr',
        may: 'May',
        june: 'Jun',
        july: 'Jul',
        august: 'Aug',
        september: 'Sep',
        october: 'Oct',
        november: 'Nov',
        december: 'Dec'
    };
    return {
        restrict: 'AE',
        scope: {
            ngModel: '=ngModel',
            culture: '=culture',
            enableShortMonth: '@'
        },
        replace: true,
        link: function ($scope, elem, attr) {
            var matchExpression = /^[0-9]+$/;
            var cultureCode = APPCONSTANTS.userPreferences.UserBasicDetails.Culture;
            //var currencyCode = APPCONSTANTS.userPreferences.UserBasicDetails.DefaultCurrencyCode;
            $scope.formatedModel = $scope.ngModel;
            var formatNumber = function () {
                $scope.formatedModel = $scope.ngModel;
                if ($scope.ngModel != undefined && $scope.ngModel != '') {
                    elem.find('.showCultureVal').autoNumeric('init', {
                        mDec: 2,
                        aPad: false,
                        aSep: thousandSep,
                        aDec: decimalSep,
                        vMin: Number.NEGATIVE_INFINITY,
                        vMax: Number.POSITIVE_INFINITY
                    });

                    elem.find('.showCultureVal').autoNumeric('set', $scope.ngModel);
                    $scope.formatedModel = elem.find('.showCultureVal').text();
                }
            };

            function extendTheLibrary() {
                if (!isLibraryExtended) {

                    if (typeof UserCountryCultureInfo != 'undefined' && UserCountryCultureInfo != "") {
                        var tempCulture = Globalize.culture().language;
                        thousandSep = Globalize.culture(UserCountryCultureInfo).numberFormat.currency[','];
                        decimalSep = Globalize.culture(UserCountryCultureInfo).numberFormat.currency['.'];
                        Globalize.culture(tempCulture);
                    }
                    try {
                        var cultureCode = _language.toLowerCase();
                        if (cultureCode == 'zh-cht') {
                            cultureCode = 'zh-hk';
                        }
                        moment().lang(cultureCode)._locale._longDateFormat['L'] = moment().lang(UserCountryCultureInfo)._locale._longDateFormat['L']
                        var x = moment.locale(cultureCode)

                        if (x == 'en' && cultureCode.indexOf('en') < 0) {
                            x = moment.locale(cultureCode.split('-')[0]);
                        }
                    }
                    catch (error) {
                        console.log(error);
                    }
                    try {
                        $.extend($.fn.autoNumeric.defaults, {
                            aSep: thousandSep,
                            aDec: decimalSep,
                            vMin: Number.NEGATIVE_INFINITY,
                            vMax: Number.POSITIVE_INFINITY,
                            mDec: MaxPrecessionValue != undefined && MaxPrecessionValue != '' ? MaxPrecessionValue * 1 : 4, //4, // hard code value, make sure updated later on getPrecessionValues callback
                            aPad: false
                        });
                        isLibraryExtended = true;
                    }
                    catch (error) {
                        console.log(error);
                    }
                }
            }
            var formatDate = function () {
                $scope.formatedModel = $scope.ngModel;
                if (attr.type == 'date' && $scope.ngModel != undefined && $scope.ngModel != '') {
                    if (matchExpression.test($scope.ngModel)) {
                        if (attr.showTime == 'true') {
                            $scope.formatedModel = moment.unix(new Date(JSON.parse($scope.ngModel)).getTime() / 1000).format(moment.localeData()._longDateFormat.L + " " + 'HH:mm');
                        } else {
                            $scope.formatedModel = moment.unix(new Date(JSON.parse($scope.ngModel)).getTime() / 1000).format(moment.localeData()._longDateFormat.L);
                        }
                    } else {
                        if (attr.showTime == 'true') {
                            $scope.formatedModel = moment.unix(new Date($scope.ngModel).getTime() / 1000).format(moment.localeData()._longDateFormat.LL + " " + 'HH:mm');
                        } else {
                            $scope.formatedModel = moment.unix(new Date($scope.ngModel).getTime() / 1000).format(moment.localeData()._longDateFormat.LL);
                        }
                    }

                    if ($scope.enableShortMonth == true && $scope.ngModel) {
                        for (var i = 0, abr = Object.keys(monthAbr); i < abr.length; i++) {
                            if ($scope.formatedModel.search(new RegExp(abr[i], 'gi')) > -1) {
                                $scope.formatedModel = $scope.formatedModel.replace(new RegExp(abr[i], 'gi'), monthAbr[abr[i]]);
                                break;
                            }
                        }
                    }
                }
            }
            
            
            extendTheLibrary();
            if (attr.type == 'date') {
                formatDate();
                $scope.$watch('ngModel', function (newValue, oldValue) {
                    if (newValue != oldValue) {
                        formatDate();
                    }
                });
            }
            else if (attr.type == "number"){
                formatNumber();               
                    $scope.$watch('ngModel', function (newValue, oldValue) {
                        if (newValue != oldValue) {
                            formatNumber();
                        }
                    });              
            }

            formatNumber();

            /*$scope.$watch('ngModel', function (newValue) {
                if (newValue) formatNumber();
            });*/

        },
        templateUrl: 'shared/directives/uiElements/smartCultureDate/smartCultureDate.html',
    };
}]);